import { objectMapToObject } from '../utils';

const current_data_version = 1;
const current_group_data_version = 1;

const updateDataVersion = (old_data) => {
	if (old_data.data_version === current_data_version) {
		return applyGroupDataUpdates(old_data);
	}
	
	let data = JSON.parse(JSON.stringify(old_data));	
	// PERFORM VERSION UPDATES IN SEQUENCE
	data.data_version = current_data_version;
	return applyGroupDataUpdates(data);
}

const applyGroupDataUpdates = (data) => ({
	...data,
	groups: data.groups.map(updateGroupDataVersion)
})

const updateGroupDataVersion = (old_group) => {
	// Everything is up to date so don't need to run update process.
	if (old_group.group_data_version === current_group_data_version) {
		return old_group;
	}

	let group = JSON.parse(JSON.stringify(old_group));
	// PERFORM VERSION UPDATES IN SEQUENCE
	if (!group.group_data_version) {
		group = ({
			...group,
			blessings: objectMapToObject(group.blessings, ([key, value]) =>
				[key, {
					id: value,
					weaponSelection: null
				}]
			)
		});
	}
	group.group_data_version = current_group_data_version;
	return group;

}

export { updateDataVersion, updateGroupDataVersion };
