import { Form, Radio, Select } from 'semantic-ui-react';

import Runemark from '../Runemarks/Runemark';
import { blessings, getBlessingById }  from './BlessingData';

import './Blessings.css';

const BlessingSelector = ({fighter, blessingId, weaponSelection, onChange}) => {
	const blessing = getBlessingById(blessingId ?? 0);

	const onBlessingChange = (value) => {
		onChange(value, null);
	}

	const onSelectedWeaponChange = (value) => {
		onChange(blessingId, value);
	}

	const getWeaponSelector = () => {
		const applicableWeapons = blessing.getApplicableWeapons(fighter);
		if (applicableWeapons == null) {
			return null;
		} else if (applicableWeapons.length === 0) {
			return (
				<div className="Blessing-error">
					No weapons applicable to this blessing.
				</div>
			);
		} else if (applicableWeapons.length === 1) {
			const weapon = applicableWeapons[0];
			if (weaponSelection !== weapon) {
				onSelectedWeaponChange(weapon);
			}
			return null;
		} else {
			return (
				<Form className="Blessings-WeaponSelector">
					{applicableWeapons.map((weaponIndex) => (
						<WeaponRadioOption
							key={weaponIndex}
							weapon={fighter.weapons[weaponIndex]}
							checked={weaponIndex === weaponSelection}
							onChange={() => onSelectedWeaponChange(weaponIndex)}
						/>
					))}
				</Form>
			)
		}
	}

	return (
		<div className="Blessings-BlessingSelector">
			<Select
				value={blessing.id}
				onChange={(e, {value}) => onBlessingChange(value)}
				options={blessings.map(blessing => ({
					key: blessing.id,
					value: blessing.id,
					text: `${blessing.name} (+${blessing.getPointsCost(fighter)})`
				}))}
			/>
			<div className="Blessings-desc">
				{blessing.effect_text}
			</div>
			{getWeaponSelector()}
		</div>
	);
}

const WeaponRadioOption = ({weapon, weaponIndex, checked, onChange}) => {
	const fancyLabel = (
		<label>
			<Runemark runemark={weapon.runemark} />
			<div className="value-container">
				<Runemark runemark="characteristics-range" />
				<div className="value">
					{weapon.range}
				</div>
			</div>
			<div className="value-container">
				<Runemark runemark="characteristics-attacks" />
				<div className="value">
					{weapon.attacks}
				</div>
			</div>
			<div className="value-container">
				<Runemark runemark="characteristics-strength" />
				<div className="value">
					{weapon.strength}
				</div>
			</div>
			<div className="value-container">
				<Runemark runemark="characteristics-damage" />
				<div className="value">
					{weapon.normal_damage}/{weapon.critical_damage}
				</div>
			</div>
		</label>
	);

	return (
		<Form.Field>
			<Radio
				label={fancyLabel}
				name="weaponRadioGroup"
				value={weaponIndex}
				checked={checked}
				onChange={onChange}
			/>
		</Form.Field>
	);
}

export default BlessingSelector;
