import RosterDragAndDropList from './RosterDragAndDropList';

import './Roster.css';

const Roster = ({
	abilities,
	fighters,
	setCurrentlyEditing,
	moveFighter,
	moveAbility,
	deleteFighter,
	deleteAbility
}) => {
	const setEditingFighter = (fighter) => setCurrentlyEditing({
		type: "FIGHTER",
		value: fighter
	});

	const setEditingAbility = (ability) => setCurrentlyEditing({
		type: "ABILITY",
		value: ability
	});

	const onDragEnd = (f) => (result) => {
		if (result.destination) {
			f(result.source.index, result.destination.index);
		}
	}

	if (fighters.length || abilities.length) {
		return (
			<div className="no-print Roster">
				<RosterDragAndDropList
						values={fighters}
						title="Fighters"
						onEdit={setEditingFighter}
						onDelete={deleteFighter}
						onDragEnd={onDragEnd(moveFighter)}/>
				<RosterDragAndDropList
						values={abilities}
						title="Abilities"
						onEdit={setEditingAbility}
						onDelete={deleteAbility}
						onDragEnd={onDragEnd(moveAbility)}/>
			</div>
		);
	} else {
		return;
	}
}

export default Roster;
