/** General object manipulation **/

export const objectMapToObject = (o, f) =>
	o ? Object.fromEntries(Object.entries(o).map(f)) : o;

export const objectMapToArray = (o, f) =>
	o ? Object.entries(o).map(f) : o;

export const objectFilter = (o, f) =>
	o ? Object.fromEntries(Object.entries(o).filter(f)) : o;

/*
* Return a new list with the element that was at prevIndex moved to newIndex.
*/
export const updateElementIndex = (arr, prevIndex, newIndex) => {
	if (prevIndex < 0 || prevIndex >= arr.length || prevIndex === newIndex) {
		return [...arr];
	} else {
		const newList = [...arr];
		newList.splice(newIndex, 0, newList.splice(prevIndex, 1)[0]);
		return newList;
	}
}

/** Reusable app-specific business logic **/

export const isAbilityActive = (ability, fighters, abilityOverrides) => 
	abilityOverrides?.[ability.id] ?? fighters.some((fighter) =>
		ability.runemarks.every((a_runemark) =>
			fighter.runemarks.includes(a_runemark)
		)
	);

export const expandWarband = (fighters, selectedFighters) => {
	const warband = [];
	fighters.forEach(fighter => {
		const fighterCount = selectedFighters[fighter.id];
		if (fighter) {
			for (let i=0; i<fighterCount; i++) {
				warband.push(fighter);
			}
		}
	});
	return warband;
}

export const isMeleeWeapon = (weapon) => {
	const rangeAsNum = +(weapon.range);
	// This assumes that a melee weapon won't have a minimum range (e.g. 1-3).
	// Technically, the rules allow this but it has never been done.
	return !isNaN(rangeAsNum) && rangeAsNum <= 3;
}
