const MonsterBracketTableEditor = ({brackets, wounds, setBrackets}) => {
    const rowMinLabel = [0, 11, 21, 31, 41];
    const rowMaxLabel = [10, 20, 30, 40, wounds - 1];

    const makeLabel = (index) => `${rowMinLabel[index]}${rowMaxLabel[index]}`;

    const onBracketChange = (idx, key) => (e) => {
        setBrackets([
            ...brackets.slice(0,idx),
            {
                ...brackets[idx],
                [key]: e.target.value
            },
            ...brackets.slice(idx + 1)
        ]);
    }

    return (
        <table className="monster-table">
            <thead>
                <tr>
                    <th>Damage Points Allocated</th>
                    <th>Move</th>
                    <th>Damage</th>
                </tr>
            </thead>
            <tbody>
                {brackets.map((bracket, idx) => (
                        <tr key={idx}>
                            <td>{makeLabel(idx)}</td>
                            <td>
								<input
									value={bracket.move}
									onChange={onBracketChange(idx, "move")} />
							</td>
                            <td>
								<input
									value={bracket.normal_damage}
									onChange={
										onBracketChange(idx, "normal_damage")
									} />
								{"/"}
								<input
									value={bracket.critical_damage}
									onChange={
										onBracketChange(idx, "critical_damage")
									} />
							</td>
                        </tr>
                    )
                )}
            </tbody>
        </table>
    );
};

export default MonsterBracketTableEditor;
