import { useState } from 'react';
import { Button, Modal, Tab } from 'semantic-ui-react';
import Runemark from './Runemark';
import './RunemarkSelector.css';

const RunemarkSelectorElement = ({runemark, onRunemarkClicked}) => (
	<Button
		className="runemark-element"
		basic
		key={runemark.name}
		onClick={onRunemarkClicked(runemark.name)} >
		<Runemark runemark={runemark.name} />
		<div className="runemark-label">
			{runemark.presentationName}
		</div>
	</Button>
)

const RunemarkSelector = ({runemarkCategories, onRunemarkSelected, children}) => {
	const [open, setOpen] = useState(false);

	const onRunemarkClicked = (runemark) => () => {
		onRunemarkSelected(runemark);
		setOpen(false);
	}

	return (
		<Modal
			className="RunemarkSelector"
			open={open}
			onClose={() => setOpen(false)}
			onOpen={() => setOpen(true)}
			trigger={children}>
			<Modal.Content>
				<Tab panes={runemarkCategories.map((runemarkCategory) => ({
					menuItem: runemarkCategory.name,
					render: () => (
						<Tab.Pane key={runemarkCategory.name}>
							<div className="runemark-list">
								{runemarkCategory.runemarks.map((runemark) =>
									<RunemarkSelectorElement
										key={runemark.name}
										runemark={runemark}
										onRunemarkClicked={onRunemarkClicked}/>
								)}
							</div>
						</Tab.Pane>
					)
				}))} />
			</Modal.Content>
		</Modal>
	)
}

export default RunemarkSelector;

