import { useMemo, useState } from 'react';

import * as runemarkUtils from '../Runemarks/runemarkUtils';
import FighterCountInput from '../Components/NumericInput';

import './FighterSelector.css';

const FighterSelector = ({fighters, selectedFighters, setSelectedFighters}) => {
	const selectedFightersData = useMemo(() => {
		const fightersData = [];
		for (const fighterId in selectedFighters) {
			const fighter = fighters.find((f) => +(f.id) === +(fighterId));
			if (fighter) {
				const count = selectedFighters[fighterId];
				fightersData.push(...Array(count).fill(fighter));
			}
		}
		return fightersData;
	}, [fighters, selectedFighters]);

	const [prevFaction, setFaction] =
		useState(getPossibleFactions(selectedFightersData)[0]);

	const [errorDropdownOpen, setErrorDropdownOpen] = useState(false);

	const faction = getBestMatchFaction(prevFaction, selectedFightersData);
	if (faction !== prevFaction) {
		setFaction(faction);
	}

	const onFighterCountChanged = (id) => (newValue) => {
		if (newValue >= 0) {
			setSelectedFighters({
				...selectedFighters,
				[id]: newValue
			});
		}
	}

	const toggleErrorDropdown = () => setErrorDropdownOpen(!errorDropdownOpen);

	const totalPoints = getTotalPoints(selectedFightersData);

	const fighterCount = selectedFightersData.length;

	const errors = getErrors(faction, selectedFightersData);
	const errorCount = errors.length;

	return (
		<div className="FighterSelector no-print">
			<div>
				Faction: {getFactionName(faction)}
			</div>
			<div className={totalPoints > 1000 ? "warning" : ""}>
				{totalPoints}{"/1000 points"}
			</div>
			<div className={(fighterCount < 3 || fighterCount > 15) ?
				"warning" : ""}>
				{`${fighterCount} fighters`}
			</div>
			{ errorCount === 0 ?
				<div className="success"> Warband validated successfully.</div>
			: errorCount === 1 ?
				<div className="warning">
					Error: {errors[0]}
				</div>	
			:
				<div className="warning">
					<div 
						className={"dropdown-header " +
							(errorDropdownOpen ? "open" : "closed")}
						onClick={toggleErrorDropdown}>
						<div className="arrow"></div>
						Errors ({errorCount})
					</div>
					{errorDropdownOpen &&
						<div className="ml-20">
							{errors.map((error, idx) =>
								<div key={idx}> {error}</div>
							)}
						</div>
					}
				</div>
			}
			<div className="mt-10">
				{fighters.map((fighter) => (
					<div key={fighter.id}>
						<FighterCountInput
							value={selectedFighters[fighter.id] || 0}
							onChange={onFighterCountChanged(fighter.id)}
							max={15}
							width="2em" />
						{fighter.name} ({fighter.points} points)
					</div>
				))}
			</div>
		</div>
	);
}

const isHero = (fighter) => fighter.runemarks.includes("fighters-leader");
const isAlly = (fighter) => fighter.runemarks.includes("fighters-ally");
const isThrall = (fighter) => fighter.runemarks.includes("fighters-thrall");
const isMonster = (fighter) => fighter.runemarks.includes("fighters-gargantuan");

const isValidLeader = (faction) => (fighter) =>
	isHero(fighter) && fighter.runemarks.includes(faction);
const isAllyable = (fighter) => isHero(fighter) || isAlly(fighter);
const isSpecial = (fighter) => isThrall(fighter) || isMonster(fighter);

const getBladebornRunemark = (fighter) => fighter.runemarks.find((runemark) =>
	runemarkUtils.isBladebornRunemark(runemark)
);

const isSameGrandAlliance = (fighter, faction) => fighter.runemarks.some(
	(runemark) => runemarkUtils.isSameGrandAlliance(runemark, faction)
);

const isLegalInWarband = (fighter, faction, otherFighters) => {
	if (fighter.runemarks.includes(faction)) return true;

	if (isSameGrandAlliance(fighter, faction)) {
		if (isAllyable(fighter)) return true;

		if (isSpecial(fighter)) return true;

		const bladebornRunemark = getBladebornRunemark(fighter);
		if (bladebornRunemark &&
			otherFighters.find((otherFighter) =>
				otherFighter.runemarks.includes(bladebornRunemark) &&
				isHero(otherFighter)
			)) return true;
	}

	return false;
}

const getPossibleFactions = (fightersData) =>
	[...new Set(fightersData.map(
		(fighter) => fighter.runemarks.filter(
			(runemark) => runemarkUtils.isFactionRunemark(runemark)
		)
	).flat())];

const getBestMatchFaction = (currentFaction, fightersData) => {
	let bestMatch = currentFaction;
	let bestMatchErrorCount = getErrors(currentFaction, fightersData).length;
	for (const faction of getPossibleFactions(fightersData)) {
		const errorCount = getErrors(faction, fightersData).length;
		if (errorCount < bestMatchErrorCount) {
			bestMatch = faction;
			bestMatchErrorCount = errorCount;
		}
	}
	return bestMatch;
}

const getFactionName = (faction) => 
	runemarkUtils.getRunemarkByName(faction)?.presentationName ?? "";

const getTotalPoints = (fightersData) =>
	fightersData.map((fighter) => +(fighter.points)).reduce((a,b) => a + b, 0);

const getErrors = (faction, fightersData) => {
	const errors = [];

	const totalPoints = getTotalPoints(fightersData);
	if (totalPoints > 1000) errors.push(`Too many points. (${totalPoints}/1000)`);

	const hasLeader = fightersData.some(isValidLeader(faction));
	if (!hasLeader) errors.push(`No leader for ${getFactionName(faction)}.`);

	const fighterCount = fightersData.length;
	if (fighterCount < 3) errors.push(`Too few fighters. (${fighterCount}/3)`);
	if (fighterCount > 15) errors.push(`Too many fighters. (${fighterCount}/15)`);

	const heroCount =  fightersData.filter(isHero).length;
	if (heroCount > 3) errors.push(`Too many heroes. (${heroCount}/3)`);

	const allyCount = fightersData
		.filter((fighter) =>
			isAllyable(fighter) && !fighter.runemarks.includes(faction))
		.length;
	if (allyCount > 2) errors.push(`Too many allies. (${allyCount}/2)`);

	const thrallCount = fightersData.filter(isThrall).length;
	if (thrallCount > 3) errors.push(`Too many thralls. (${thrallCount}/3)`);

	const monsterCount = fightersData.filter(isMonster).length;
	if (monsterCount > 1) errors.push(`Too many monsters. (${monsterCount}/1)`);

	const illegalFighters = fightersData.filter(
		(fighter) => !isLegalInWarband(fighter, faction, fightersData));
	errors.push(...illegalFighters.map((fighter) =>
		`${fighter.name} is not legal in ${getFactionName(faction)}`));

	return errors;
}

export default FighterSelector;
