import { useState } from "react";
import { Button } from "semantic-ui-react";

import FighterCard from "./FighterCard";
import AbilityCard from "./AbilityCard";
import { getFighters, getAbilities } from './referenceSheetCommon';

import './ReferenceSheet.css';

const Preview = ({group, printOptions}) => {
	const [showPreview, setShowPreview] = useState(false);
	const fighters = getFighters(group, printOptions);
	const abilities = getAbilities(group, fighters, printOptions);

	return (
		<div className="ReferenceSheet no-print mt-10 mb-10">
			{ (fighters.length > 0 || abilities.length > 0) &&
				<Button onClick={() => setShowPreview(!showPreview)}>
					{showPreview? "Hide Preview" : "Show Preview"}
				</Button>
			}
			{ showPreview &&
				<>
					{ fighters.length > 0 &&
						<>
							<h3 >Fighters</h3>
							<div className="page card-list">
								{fighters.map((fighter, idx) => (
									<FighterCard
										key={idx}
										fighter={fighter}
										printOptions={printOptions} />
								))}
							</div>
						</>
					}
					{ abilities.length > 0 &&
						<>
							<h3 >Abilities</h3>
							<div className="page card-list">
								{abilities.map((ability) => (
									<AbilityCard key={ability.id} ability={ability} />
								))}
							</div>
						</>
					}
				</>
			}
		</div>
	);
}

export default Preview;
