export class PrintLayout {
	static SEPARATE_PAGES = new PrintLayout(
		"SEPARATE_PAGES", "Separate Pages");
	static FEWEST_PAGES = new PrintLayout(
		"FEWEST_PAGES", "Fewest Pages");
	static SEPARATE_COLUMNS = new PrintLayout(
		"SEPARATE_COLUMNS", "Separate Columns");

	static layouts = [
		PrintLayout.SEPARATE_PAGES,
		PrintLayout.SEPARATE_COLUMNS,
		PrintLayout.FEWEST_PAGES,
	]

	static getPrintLayout = (printOptions) => {
		return PrintLayout.layouts.find(
			(layout) => layout.value === printOptions?.layout
		) ?? PrintLayout.SEPARATE_PAGES;
	}


	constructor(value, presentationName) {
		this.value = value;
		this.presentationName = presentationName;
	}
}

export const shouldHideDropShadow = (printOptions) => {
	return printOptions.hideDropShadow ?? false;
}

export const shouldSortAbilities = (printOptions) => {
	return printOptions.sortAbilities ?? true;
}

export const shouldShowBlessings = (printOptions) => {
	return printOptions.showBlessings ?? false;
}

export const shouldShowDuplicateFighters = (printOptions) => {
	return printOptions.showDuplicateFighters ?? false;
}

export const shouldInvertMonsterTables = (printOptions) => {
	return printOptions.invertMonsterTables ?? false;
}
