const getDefaultWeapon = () => ({
	runemark: "",
	range: "",
	attacks: "",
	strength: "",
	normal_damage: "",
	critical_damage: "",
});

const getDefaultFighter = (id) => ({
	id,
	name: "",
	long_name_format: false,
	points: 0,
	move: "",
	toughness: "",
	wounds: "",
	runemarks: [],
	weapons: [ getDefaultWeapon() ]
});

const MONSTER_PLACEHOLDER_VALUE = "";

const getDefaultMonsterWeapon = () => {
	const weapon = getDefaultWeapon();
	weapon.normal_damage = MONSTER_PLACEHOLDER_VALUE;
	weapon.critical_damage = MONSTER_PLACEHOLDER_VALUE;
	return weapon;
}

const getDefaultMonsterBracket = () => ({
	move: "",
	normal_damage: "",
	critical_damage: ""
})

const getDefaultMonster = (id) => ({
	id,
	is_monster: true,
	name: "",
	long_name_format: false,
	points: 0,
	move: MONSTER_PLACEHOLDER_VALUE,
	toughness: "",
	wounds: "45",
	runemarks: [],
	weapons: [ getDefaultMonsterWeapon() ],
	brackets: [
		getDefaultMonsterBracket(),
		getDefaultMonsterBracket(),
		getDefaultMonsterBracket(),
		getDefaultMonsterBracket(),
		getDefaultMonsterBracket()
	]
});

const getDefaultAbility = (id) => ({
	id,
	name: "",
	type: "",
	runemarks: [],
	text: ""
});

export { getDefaultWeapon, getDefaultFighter, getDefaultMonster, getDefaultAbility };
