import { Button, Checkbox } from 'semantic-ui-react';

import {isAbilityActive} from '../utils';

import './AbilitySelector.css';

const AbilitySelector = ({
	fighters, selectedFighters, abilities, abilityOverrides, setAbilityOverrides
}) => {

	const onAbilityCheckChanged = (id) => (e, {checked}) => {
		setAbilityOverrides({
			...(abilityOverrides || {}),
			[id]: checked
		});
	}

	const onReset = () => {
		setAbilityOverrides({});
	}
	
	const activeFighters = fighters.filter(
		(fighter) => selectedFighters[fighter.id]
	);

	return (
		<div className="AbilitySelector">
			<div>
				<Button onClick={onReset}>
					Reset
				</Button>
			</div>
			{abilities.map((ability) => (
				<div key={ability.id} className="ability-selector-row">
					<Checkbox
						id={`checkbox-${ability.id}`}
						type="checkbox"
						checked={isAbilityActive(
							ability, activeFighters, abilityOverrides || {}
						)}
						onChange={onAbilityCheckChanged(ability.id)}
					/>
					<label
						htmlFor={`checkbox-${ability.id}`}
						className="ability-selector-name">
						{ability.name}
					</label>
				</div>
			))}
		</div>	
	);
}

export default AbilitySelector;
