import { useState } from 'react';

import {fighterApplicableRunemarkCategories} from '../../Runemarks/runemarkList';
import Runemark from '../../Runemarks/Runemark';
import RunemarkSelector from '../../Runemarks/RunemarkSelector';

const RunemarksEditor = ({value, onChange}) => {
	const [currentHover, setCurrentHover] = useState(null);

	const addRunemark = (runemark) => {
		if (!value.includes(runemark)) {
			onChange([...value, runemark]);
		}
	}	

	const deleteRunemark = (toDelete) => {
		onChange(value.filter((runemark) => runemark !== toDelete));
	}

	return (
		<div className="RunemarksEditor">
			{value.map((runemark) => (
				<button
					className="RunemarksEditor-button"
					key={runemark}
					onClick={() => deleteRunemark(runemark)}
					onMouseEnter={() => setCurrentHover(runemark)}
					onMouseLeave={() => setCurrentHover(null)}
					onFocus={() => setCurrentHover(runemark)}
					onBlur={() => setCurrentHover(null)}
					>
					<Runemark runemark={runemark}/>
					{ runemark === currentHover &&
						<div className="RunemarksEditor-delete"
							alt="Delete Runemark"
							title="Delete Runemark">
							
						</div>
					}
				</button>
			))}
			<RunemarkSelector
				runemarkCategories={fighterApplicableRunemarkCategories}
				onRunemarkSelected={addRunemark}>
				<button className="RunemarksEditor-add">+</button>
			</RunemarkSelector>
		</div>
	);
}

export default RunemarksEditor;
