import './Runemark.css';
import { missingRunemarkRunemark } from './runemarkList';
import { getRunemarkByName } from './runemarkUtils';

const Runemark = ({runemark}) => {
	const runemarkData = getRunemarkByName(runemark) ?? missingRunemarkRunemark;
	return (
		<img
			className="runemark"
			src={runemarkData.src}
			alt={`${runemarkData.presentationName} Runemark`}
			title={`${runemarkData.presentationName} Runemark`}/>
	);
}

export default Runemark;
