import { useState } from 'react';
import { Button, Form, Modal, TextArea } from 'semantic-ui-react';

import { updateGroupDataVersion } from '../state/data_versioning';


const GroupImportExport = ({currentGroup, updateGroup, getNextGroupId}) => {
	const [importModalOpen, setImportModalOpen] = useState(false);
	const [importText, setImportText] = useState("");

	const encode = (s) => JSON.stringify(s);
	const decode = (s) => JSON.parse(s);

	const exportGroup = () => {
		navigator.clipboard.writeText(encode(currentGroup));
		//TODO: replace this with a Toast or something.
		alert(`${currentGroup.name} copied to clipboard.`);
	}

	const importGroup = (groupEncodedString) => {
		const importedGroup = decode(importText);
		const id = getNextGroupId();
		importedGroup.id = id;
		updateGroup(updateGroupDataVersion(importedGroup));
		setImportText("");
		setImportModalOpen(false);
	}

	//TODO: Add option to import from other data sources, like BattleScribe?
	return (
		<>
			<Modal
				trigger={
					<button className="icon-button" title="Import Roster Data">
						
					</button>
				}
				open={importModalOpen}
				onOpen={() => setImportModalOpen(true)}
				onClose={() => setImportModalOpen(false)}>
				<Modal.Header>
					Import Roster
				</Modal.Header>
				<Modal.Content>
					<Form>
						<TextArea
							value={importText}
							onChange={(e, {value}) => setImportText(value)}
							rows="15" />
					</Form>
				</Modal.Content>
				<Modal.Actions>
					<Button onClick={importGroup}>
						Import
					</Button>
				</Modal.Actions>
			</Modal>
			{ currentGroup &&
				<button className="icon-button"
					onClick={exportGroup}
					title="Export Roster Data">
					
				</button>
			}
		</>
	);
}

export default GroupImportExport;
