import {weaponRunemarks} from "../../Runemarks/runemarkList";
import Runemark from '../../Runemarks/Runemark';
import RunemarkSelector from "../../Runemarks/RunemarkSelector";

const WeaponEditor = ({weapon, onChange, onDelete, showDelete=false, isMonster}) => {

	const onValueChange = (value) => (e) => {
		onChange({
			...weapon,
			[value]: e.target.value
		});
	}

	const onRunemarkSelected = (runemark) => {
		onChange({...weapon, runemark});
	}

	return (
		<div className="WeaponEditor">
			<div className="WeaponEditor-weapon-row">
				<RunemarkSelector
					runemarkCategories={[{
						name: "Weapons",
						runemarks: weaponRunemarks
					}]}
					onRunemarkSelected={onRunemarkSelected}>
					<button className="WeaponEditor-runemark-button">
						<Runemark runemark={weapon.runemark} />
					</button>
				</RunemarkSelector>
				<div className="WeaponEditor-weapon-divider" > </div>
				<div className="WeaponEditor-runemark-value" >
					<Runemark runemark="characteristics-range" />
					<input
						id="range"
						className="value range-input"
						value={weapon.range}
						onChange={onValueChange("range")}/>
				</div>
				<div className="WeaponEditor-weapon-divider" > </div>
				<div className="WeaponEditor-runemark-value" >
					<Runemark runemark="characteristics-attacks" />
					<input className="value"
						id="attacks"
						value={weapon.attacks}
						onChange={onValueChange("attacks")}/>
				</div>
				<div className="WeaponEditor-weapon-divider" > </div>
				<div className="WeaponEditor-runemark-value" >
					<Runemark runemark="characteristics-strength" />
					<input className="value"
						id="strength"
						value={weapon.strength}
						onChange={onValueChange("strength")}/>
				</div>
				<div className="WeaponEditor-weapon-divider" > </div>
				<div className="WeaponEditor-runemark-value" >
					<Runemark runemark="characteristics-damage" />
					<div className="value">
						<input
							id="normal_damage"
							disabled={isMonster}
							value={weapon.normal_damage}
							onChange={onValueChange("normal_damage")}/>
						{"/"}
						<input
							id="critical_damage"
							disabled={isMonster}
							value={weapon.critical_damage}
							onChange={onValueChange("critical_damage")}/>
					</div>
				</div>
				{showDelete &&
					<button
						className="delete icon-button"
						onClick={onDelete} />
				}
			</div>
		</div>
	);
};

export default WeaponEditor;
