import { useState } from 'react';

import { Button } from "semantic-ui-react";

import FighterEditor from "./Editor/FighterEditor";
import AbilityEditor from "./Editor/AbilityEditor";
import Importer from "./Editor/Importer";
import Roster from "./Roster/Roster";
import {
	getDefaultFighter,
	getDefaultMonster,
	getDefaultAbility
} from './defaultValues';

import './RosterBuilder.css';

const Editor = ({
    otherGroups,
    fighters,
    abilities,
    saveAbility,
    saveFighter,
    moveAbility,
    moveFighter,
    deleteAbility,
    deleteFighter,
	batchSaveFighters,
	batchSaveAbilities,
}) => {
	const [ currentlyEditing, setCurrentlyEditing ] = useState(null);

	const getNextId = (prevId, idsArray) => {
		let nextId = prevId + 1;
		let usedIds = new Set(idsArray);
		while(true) {
			if (usedIds.has(nextId)) {
				nextId++;
			} else {
				return nextId;
			}
		}
	}

	const getNextFighterId = (prevId = 0) =>
		getNextId(prevId, fighters.map(fighter => fighter.id));

	const getNextAbilityId = (prevId = 0) =>
		getNextId(prevId, abilities.map(ability => ability.id));

	const createFighter = () => setCurrentlyEditing({
		type: "FIGHTER",
		value: getDefaultFighter(getNextFighterId())
	});

	const createMonster = () => setCurrentlyEditing({
		type: "FIGHTER",
		value: getDefaultMonster(getNextFighterId())
	});

	const createAbility = () => setCurrentlyEditing({
		type: "ABILITY",
		value: getDefaultAbility(getNextAbilityId())
	});

	const openAbilityImporter = () => setCurrentlyEditing({type: "ABILITY_IMPORT"})
	const openFighterImporter = () => setCurrentlyEditing({type: "FIGHTER_IMPORT"})

	const exitEditor = () => setCurrentlyEditing(null);

	const importAbilities = (abilities) => {
		let prevId = 0;
		batchSaveAbilities(abilities.map((ability) => {
			prevId = getNextAbilityId(prevId);
			return {...ability, id: prevId};
		}));
	}

	const importFighters = (fighters) => {
		let prevId = 0;
		batchSaveFighters(fighters.map((fighter) => {
			prevId = getNextFighterId(prevId);
			return {...fighter, id: prevId};
		}));
	}

	let editorContent = null;
	if (currentlyEditing?.type === "FIGHTER") {
		editorContent = (
			<FighterEditor
				fighter={currentlyEditing.value}
				saveFighter={saveFighter}
				deleteFighter={() => deleteFighter(currentlyEditing.value.id)}
				exitEditor={exitEditor}
				key={currentlyEditing.value.id}
			/>
		);
	} else if (currentlyEditing?.type === "ABILITY") {
		editorContent = (
			<AbilityEditor
				ability={currentlyEditing.value}
				saveAbility={saveAbility}
				exitEditor={exitEditor}
				deleteAbility={() => deleteAbility(currentlyEditing.value.id)}
				key={currentlyEditing.value.id}
			/>
		);
	} else if (currentlyEditing?.type === "ABILITY_IMPORT") {
		editorContent = (
			<Importer
				importableData={otherGroups.map((group) => ({
					name: group.name,
					data: group.abilities
				}))}
				onSubmit={importAbilities}
				onClose={exitEditor}
			/>
		);
	} else if (currentlyEditing?.type === "FIGHTER_IMPORT") {
		editorContent = (
			<Importer
				importableData={otherGroups.map((group) => ({
					name: group.name,
					data: group.fighters
				}))}
				onSubmit={importFighters}
				onClose={exitEditor}
			/>
		);
	} else {
		editorContent = (
			<div className="Editor-button-container">
				<Button onClick={createFighter}>
					Add Fighter
				</Button>
				<Button onClick={createMonster}>
					Add Monster
				</Button>
				<Button onClick={createAbility}>
					Add Ability
				</Button>
				<Button onClick={openFighterImporter}
					disabled={otherGroups.length === 0}>
					Import Fighters
				</Button>
				<Button onClick={openAbilityImporter}
					disabled={otherGroups.length === 0}>
					Import Abilities
				</Button>
			</div>
		);
	}
	return (
		<div className="RosterBuilder no-print">
			{editorContent}
			<Roster
				fighters={fighters}
				abilities={abilities}
				setCurrentlyEditing={setCurrentlyEditing}
				moveFighter={moveFighter}
				moveAbility={moveAbility}
				deleteFighter={deleteFighter}
				deleteAbility={deleteAbility}
			/>
		</div>
	);
}

export default Editor;
