import { PrintLayout } from '../state/printOptions';
import { getFighters, getAbilities } from './referenceSheetCommon';
import FighterCard from "./FighterCard";
import AbilityCard from "./AbilityCard";

import './ReferenceSheet.css';

const ReferenceSheet = ({group, printOptions}) => {
	const fighters = getFighters(group, printOptions);
	const abilities = getAbilities(group, fighters, printOptions);

	const fighterCards = fighters.map((fighter, idx) => (
		<FighterCard
			key={`${idx}`}
			fighter={fighter}
			printOptions={printOptions} />
	));

	const abilityCards = abilities.map((ability) => (
		<AbilityCard
			key={`ability-${ability.id}`}
			ability={ability}
			printOptions={printOptions} />
	));

	const printLayout = PrintLayout.getPrintLayout(printOptions);

	switch (printLayout) {
		case PrintLayout.SEPARATE_PAGES:
			return (
				<div className="ReferenceSheet print-only">
					<div className="page card-list">
						{fighterCards}
					</div>
					<div className="page card-list">
						{abilityCards}
					</div>
				</div>
			);
		case PrintLayout.SEPARATE_COLUMNS:
			return (
				<div className="ReferenceSheet print-only">
					<div className="page columns">
						<div className="column">
							{fighterCards}
						</div>
						<div className="column">
							{abilityCards}
						</div>
					</div>
				</div>
			);
		case PrintLayout.FEWEST_PAGES:
			return (
				<div className="ReferenceSheet print-only">
					<div className="page card-list">
						{fighterCards}
						<div className="mt-20" />
						{abilityCards}
					</div>
				</div>
			);
		default:
			return (
				<div></div>
			);
	}
}

export default ReferenceSheet;
