import { useState } from 'react';
import { Select } from 'semantic-ui-react';

import { Button, Modal } from 'semantic-ui-react';
import GroupImportExport from './GroupImportExport';

import './GroupSelector.css';

const GroupSelector = ({
	groups,
	currentGroup,
	setCurrentGroup,
	updateGroup,
	deleteGroup,
}) => {
	const [modalOpen, setModalOpen] = useState(false);
	const [editName, setEditName] = useState("");
	const [editId, setEditId] = useState(null);

	const startEditingGroup = (id, name, runemark) => {
		setModalOpen(true);
		setEditId(id);
		setEditName(name);
	}

	const getNextGroupId = () => 1 + Math.max(0, ...groups.map((group) => group.id));

	const addGroup = () => startEditingGroup(getNextGroupId(), "", "");

	const onEditCancel = () => {
		setModalOpen(false);
		setEditName("");
		setEditId(null);
	}

	const onEditFinish = () => {
		setModalOpen(false);
		updateGroup({ id: editId, name: editName });
	}

	const getModalTitle = () => {
		const group = groups.find((group) => group.id === editId);
		return group ? `Editing ${group.name}` : `Creating Roster`;
	}

	return (
		<div className="GroupSelector no-print">
			<div className="GroupSelector-dropdown">
				{currentGroup ?
					<Select value={currentGroup.id}
						onChange={(e, {value}) => setCurrentGroup(value)}
						options={
							groups.map((group) => ({
								key: group.id, value: group.id, text: group.name
							}))
						} />
					: "No existing rosters. Create or import one to get started: "
				}
			</div>
			<div className="GroupSelector-buttons">
				{currentGroup && 
					<>
						<button className="icon-button edit"
							title="Edit Roster"
							onClick={() => startEditingGroup(
								currentGroup.id,
								currentGroup.name,
								currentGroup.runemark
							)} />
						<button className="icon-button delete" title="Delete Roster"
							onClick={() => deleteGroup(currentGroup.id)} />
					</>
				}
				<button className="icon-button"
					onClick={addGroup}
					title="Create Roster">
					
				</button>
				<GroupImportExport
					currentGroup={currentGroup}
					updateGroup={updateGroup}
					getNextGroupId={getNextGroupId} />
			</div>
			<Modal
				className="GroupSelector-editor"
				open={modalOpen}
				onClose={onEditCancel}>
				<Modal.Header>
					{getModalTitle()}
				</Modal.Header>
				<Modal.Content>
					<label htmlFor="GroupSelector-editor-name">Name:</label>
					<input
						id="GroupSelector-editor-name"
						type="text"
						value={editName}
						onChange={(e) => setEditName(e.target.value)}
						placeholder="Roster Name" />
				</Modal.Content>
				<Modal.Actions>
					<Button onClick={onEditFinish}>
						Save Roster
					</Button>
				</Modal.Actions>
			</Modal>
		</div>
	)

}

export default GroupSelector;
