import {isMeleeWeapon} from '../utils';

const SELECTION_NONE = 0;
const SELECTION_MELEE = 1;
const SELECTION_ANY_WEAPON = 2;

class Blessing {
	constructor(
		id, name, effect_text, selection_type, cost_1, cost_2, apply
	) {
		this.id = id;
		this.name = name;
		this.effect_text = effect_text;
		this.selection_type = selection_type;
		this._cost_1 = cost_1;
		this._cost_2 = cost_2;
		this.apply = apply;
	}

	/*
	 * Returns:
	 *   null if there shouldn't be a selection,
	 *   [] if there should be a selection, but no options are approriate,
	 *   [ index... ] indices of weapons if there are one or more appropriate weapons.
	 */
	getApplicableWeapons(fighter) {
		if (this.selection_type === SELECTION_ANY_WEAPON) {
			return fighter.weapons.map((weapon, idx) => idx);
		} else if (this.selection_type === SELECTION_MELEE) {
			return fighter.weapons.map((weapon, idx) =>
				isMeleeWeapon(weapon) ? idx : null).filter((x) => x !== null);
		} else {
			return null;
		}
	}

	getPointsCost(fighter) {
		return getPointsCost(fighter, this._cost_1, this._cost_2);
	}
}

const NONE = new Blessing(
	0, "No blessing", "", SELECTION_NONE, 0, 0, (x) => x
);
const SWIFTNESS = new Blessing(
	1, "Swiftness", "+1 Move", SELECTION_NONE, 15, 25,
	(fighter) => ({
		...fighter,
		name: `Swift ${fighter.name}`,
		move: +(fighter.move) + 1,
		points: +(fighter.points) + getPointsCost(fighter, 15, 25)
	})
);
const RESILIENCE = new Blessing(
	2, "Resilience", "+1 Toughness", SELECTION_NONE, 15, 20,
	(fighter) => ({
		...fighter,
		name: `Resilient ${fighter.name}`,
		toughness: +(fighter.toughness) + 1,
		points: +(fighter.points) + getPointsCost(fighter, 15, 20)
	})
);
const FORTITUDE = new Blessing(
	3, "Fortitude", "+4 Wounds", SELECTION_NONE, 15, 15,
	(fighter) => ({
		...fighter,
		name: `Fortitudinous ${fighter.name}`,
		wounds: +(fighter.wounds) + 4,
		points: +(fighter.points) + 15
	})
);
const FEROCITY = new Blessing(
	4, "Ferocity", "+1 Attacks", SELECTION_MELEE, 25, 30,
	(fighter, selection) => ({
		...fighter,
		name: `Ferocious ${fighter.name}`,
		points: +(fighter.points) + getPointsCost(fighter, 25, 30),
		weapons: fighter.weapons.map((weapon, idx) =>
			idx === selection ? {
				...weapon,
				attacks: +(weapon.attacks) + 1
			} : weapon)
	})
);
const MIGHT = new Blessing(
	5, "Might", "+1 Strength", SELECTION_ANY_WEAPON, 15, 20,
	(fighter, selection) => ({
		...fighter,
		name: `Mighty ${fighter.name}`,
		points: +(fighter.points) + getPointsCost(fighter, 15, 20),
		weapons: fighter.weapons.map((weapon, idx) =>
			idx === selection ? {
				...weapon,
				strength: +(weapon.strength) + 1
			} : weapon)
	})
);
const BRUTALITY = new Blessing(
	6, "Brutality", "+1 Normal Damage", SELECTION_MELEE, 25, 30,
	(fighter, selection) => ({
		...fighter,
		name: `Brutal ${fighter.name}`,
		points: +(fighter.points) + getPointsCost(fighter, 25, 30),
		weapons: fighter.weapons.map((weapon, idx) =>
			idx === selection ? {
				...weapon,
				normal_damage: +(weapon.normal_damage) + 1
			} : weapon)
	})
);
const SAVAGERY = new Blessing(
	7, "Savagery", "+1 Critical Damage", SELECTION_MELEE, 10, 10,
	(fighter, selection) => ({
		...fighter,
		name: `Savage ${fighter.name}`,
		points: +(fighter.points) + 10,
		weapons: fighter.weapons.map((weapon, idx) =>
			idx === selection ? {
				...weapon,
				critical_damage: +(weapon.critical_damage) + 1
			} : weapon)
	})
);
const HEROISM = new Blessing(
	8, "Heroism", "Can use Inspiring Presence as if it was a Hero",
	SELECTION_NONE, 10, 10,
	(fighter) => ({
		...fighter,
		name: `Heroic ${fighter.name}`,
		points: +(fighter.points) + 10
	})
);
const SPITE = new Blessing(
	9, "Spite", "One free reaction per round", SELECTION_NONE, 30, 30,
	(fighter) => ({
		...fighter,
		name: `Spiteful ${fighter.name}`,
		points: +(fighter.points) + 30
	})
);
const DESTINY = new Blessing(
	10, "Destiny", "+1 Wild Dice", SELECTION_NONE, 40, 40,
	(fighter) => ({
		...fighter,
		name: `Destined ${fighter.name}`,
		points: +(fighter.points) + 40
	})
);

const getPointsCost = (fighter, cost_1, cost_2) => 
	fighter.wounds < 23 ? cost_1 : cost_2;

const blessings = [
	NONE,
	SWIFTNESS,
	RESILIENCE,
	FORTITUDE,
	FEROCITY,
	MIGHT,
	BRUTALITY,
	SAVAGERY,
	HEROISM,
	SPITE,
	DESTINY
];

const getBlessingById = (id) => blessings.find(blessing => blessing.id === id);

export { blessings, getBlessingById };
