const reducer = (state, action) => {
	switch(action.type) {
		case 'UPDATE_FIGHTERS':
			return ({
				...state,
				groups: state.groups.map((group) =>
					group.id === state.currentGroup ?
						{...group, fighters: action.fighters} :
						group
				)
			});
		case 'UPDATE_ABILITIES':
			return ({
				...state,
				groups: state.groups.map((group) =>
					group.id === state.currentGroup ?
						{...group, abilities: action.abilities} :
						group
				)
			});
		case 'UPDATE_SELECTED_FIGHTERS':
			return ({
				...state,
				groups: state.groups.map((group) =>
					group.id === state.currentGroup ?
						{
							...group,
							selectedFighters: action.selectedFighters,
							blessings: {}
						} : group
				)
			});
		case 'SET_BLESSINGS':
			return ({
				...state,
				groups: state.groups.map((group) =>
					group.id === state.currentGroup ?
						{
							...group,
							blessings: action.blessings
						} : group
				)
			});
		case 'ADD_GROUP':
			return ({
				...state,
				currentGroup: action.group.id,
				groups: [
					...state.groups,
					{
						name: "",
						runemark: "",
						fighters: [],
						abilities: [],
						selectedFighters: {},
						abilityOverrides: {},
						...action.group
					}
				]
			});
		case 'UPDATE_GROUP':
			return ({
				...state,
				currentGroup: action.group.id,
				groups: state.groups.map((group) =>
					group.id === action.group.id ?
					({ ...group, ...action.group }) :
					group
				)
			});
		case 'DELETE_GROUP':
			const newGroups = state.groups.filter((group) => group.id !== action.id);
			return ({
				...state,
				currentGroup: newGroups[0]?.id,
				groups: newGroups
			});
		case 'SET_CURRENT_GROUP':
			return ({
				...state,
				currentGroup: action.id
			});
		case 'SET_ABILITY_OVERRIDES':
			return ({
				...state,
				groups: state.groups.map((group) =>
					group.id === state.currentGroup ?
						{...group, abilityOverrides: action.abilityOverrides} :
						group
				)
			});
		case 'SET_PRINT_OPTIONS':
			return ({
				...state,
				printOptions: action.printOptions,
			});
		default:
			return state;
	}
}

export default reducer;
