import { useState } from 'react';
import { Button, Checkbox } from 'semantic-ui-react';

import { getDefaultWeapon } from '../defaultValues';
import RunemarksEditor from './RunemarksEditor';
import WeaponEditor from './WeaponEditor';
import MonsterBracketTableEditor from './MonsterBracketTableEditor';
import NumericInput from '../../Components/NumericInput';

const FighterEditor = ({fighter, saveFighter, deleteFighter, exitEditor}) => {
	const [name, setName] = useState(fighter.name);
	const [longNameFormat, setLongNameFormat] = useState(fighter.long_name_format);
	const [points, setPoints] = useState(fighter.points);
	const [move, setMove] = useState(fighter.move);
	const [toughness, setToughness] = useState(fighter.toughness);
	const [wounds, setWounds] = useState(fighter.wounds);
	const [runemarks, setRunemarks] = useState(fighter.runemarks);
	const [weapons, setWeapons] = useState(fighter.weapons);
	const [brackets, setBrackets] = useState(fighter.brackets);
	
	const onSave = () => {
		saveFighter({
			id: fighter.id,
			is_monster: fighter.is_monster,
			name,
			long_name_format: longNameFormat,
			points,
			move,
			toughness,
			wounds,
			runemarks,
			weapons,
			brackets
		});
		exitEditor();
	}

	const onDelete = () => {
		deleteFighter();
		exitEditor();
	}

	const addNewWeapon = () => {
		setWeapons([...weapons, getDefaultWeapon()]);
	}

	const onWeaponChange = (index) => (weapon) => {
		setWeapons([
			...weapons.slice(0, index),
			weapon,
			...weapons.slice(index + 1)
		]);
	}

	const deleteWeapon = (index) => {
		setWeapons([
			...weapons.slice(0, index),
			...weapons.slice(index + 1)
		]);
	}

	const onChange = (f) => (e) => {
		f(e.target.value);
	}

	const onCheckboxChange = (f) => (e, {checked}) => {
		f(checked);
	}

	return (
		<div className="FighterEditor">
			<div>
				<label htmlFor="name">Name:</label>
				<input
					id="name"
					value={name}
					onChange={onChange(setName)}
					type="text"/>
			</div>
			<div className="long-name">
				<Checkbox
					label="Use Long Name Formatting"
					id="longNameFormatCheckbox"
					checked={longNameFormat}
					onChange={onCheckboxChange(setLongNameFormat)}
					type="checkbox" />
			</div>
			<div>
				<label htmlFor="Points">Points:</label>
				<NumericInput
					id="points"
					value={points}
					min={0}
					max={1000}
					increment={5}
					onChange={setPoints} />
			</div>
			<div>
				<label htmlFor="move">Movement:</label>
				<NumericInput
					id="move"
					disabled={fighter.is_monster}
					value={move}
					validate={false}
					onChange={setMove} />
			</div>
			<div>
				<label htmlFor="toughness">Toughness:</label>
				<NumericInput
					id="toughness"
					value={toughness}
					onChange={setToughness} />
			</div>
			<div>
				<label htmlFor="wounds">Wounds:</label>
				<NumericInput
					id="wounds"
					min={fighter.is_monster ? 45 : 0}
					max={256}
					value={wounds}
					onChange={setWounds} />
			</div>
			<div className="FighterEditor-runemark-row">
				<label>Runemarks:</label>
				<RunemarksEditor
					value={runemarks}
					onChange={setRunemarks} />
			</div>
			<div>
				<label>Weapon(s):</label>
				{weapons.map((weapon, idx) =>
					<WeaponEditor
						isMonster={fighter.is_monster}
						showDelete={weapons.length > 1}
						key={idx}
						weapon={weapon}
						onChange={onWeaponChange(idx)}
						onDelete={() => deleteWeapon(idx)}/>
				)}
				{!fighter.is_monster && weapons.length < 2 &&
					<div className="mt-5">
						<Button onClick={addNewWeapon}>Add Weapon</Button>
					</div>
				}
				{fighter.is_monster && 
					<MonsterBracketTableEditor
						brackets={brackets}
						wounds={wounds}
						setBrackets={setBrackets} />
				}
			</div>
			<div className="mt-10">
				<Button onClick={onSave}>Save</Button>
				<Button onClick={onDelete}>Delete</Button>
				<Button onClick={exitEditor}>Exit</Button>
			</div>
		</div>
	);
}

export default FighterEditor;
