import { useState } from "react";

import { Button, Checkbox, Select } from "semantic-ui-react";

const Importer = ({importableData, onSubmit, onClose}) => {
	const [currentGroup, _setCurrentGroup] = useState(0);
	const [selectedData, setSelectedData] = useState([]);

	const setCurrentGroup = (groupIndex) => {
		_setCurrentGroup(groupIndex);
		setSelectedData([]);
	}

	const toggleDataSelection = (dataIndex) => () => {
		if (selectedData.includes(dataIndex)) {
			setSelectedData(selectedData.filter((e) => e !== dataIndex));
		} else {
			setSelectedData([ ...selectedData, dataIndex]);
		}
	}

	const importSelectedData = () => {
		onSubmit(importableData[currentGroup].data.filter(
			(_, index) => selectedData.includes(index)
		));
		onClose();
	}
	
	return (
		<div className="Importer">
			<div>
				Roster to import from:
				<Select
					value={currentGroup}
					onChange={(e, {value}) => setCurrentGroup(value)}
					options={
						importableData.map((group, index) => ({
							key: index, value: index, text: group.name
						}))
					} />
			</div>
			<div>
				{importableData[currentGroup].data.map((e, dataIndex) => (
					<div key={dataIndex}>
						<Checkbox
							checked={selectedData.includes(dataIndex)}
							onChange={toggleDataSelection(dataIndex)}
							label={e.name} />
					</div>
				))} 
			</div>
			<Button
				disabled={selectedData.length === 0}
				onClick={importSelectedData}>
				Import
			</Button>
			<Button onClick={onClose}>
				Cancel
			</Button>
		</div>
	);
}

export default Importer;
