export const updateFighters = (fighters) => ({
	type: 'UPDATE_FIGHTERS',
	fighters
});

export const updateAbilities = (abilities) => ({
	type: 'UPDATE_ABILITIES',
	abilities
});

export const updateSelectedFighters = (selectedFighters) => ({
	type: 'UPDATE_SELECTED_FIGHTERS',
	selectedFighters
});

export const addGroup = (group) => ({
	type: 'ADD_GROUP',
	group
});

export const updateGroup = (group) => ({
	type: 'UPDATE_GROUP',
	group
});

export const deleteGroup = (id) => ({
	type: 'DELETE_GROUP',
	id
});

export const setCurrentGroup = (id) => ({
	type: 'SET_CURRENT_GROUP',
	id: +id
});

export const setAbilityOverrides = (abilityOverrides) => ({
	type: 'SET_ABILITY_OVERRIDES',
	abilityOverrides
});

export const setPrintOptions = (printOptions) => ({
	type: 'SET_PRINT_OPTIONS',
	printOptions
});

export const setBlessings = (blessings) => ({
	type: 'SET_BLESSINGS',
	blessings
});
