import { Button, Checkbox, Select } from 'semantic-ui-react';
import {
	PrintLayout,
	shouldHideDropShadow,
	shouldSortAbilities,
	shouldShowBlessings,
	shouldShowDuplicateFighters,
	shouldInvertMonsterTables
} from '../state/printOptions';

const PrintOptions = ({ printOptions, setPrintOptions }) => {
	const onPrint = () => window.print();

	const printLayout = PrintLayout.getPrintLayout(printOptions);

	const onPrintLayoutChanged = (e, {value}) => {
		setPrintOptions({
			...printOptions,
			layout: value
		});
	}

	const onCheckOptionChanged = (option) => (e, {checked}) => {
		setPrintOptions({
			...printOptions,
			[option]: checked
		});
	}

	return (
		<div className="PrintOptions">
			<div>
				<label htmlFor="printLayoutSelector" className="mr-10">
					Layout:
				</label>
				<Select id="printLayoutSelector"
					value={printLayout.value}
					onChange={onPrintLayoutChanged}
					label="Layout:"
					options={PrintLayout.layouts.map((layout) => ({
						key: layout.value,
						value: layout.value,
						text: layout.presentationName
					}))} />
			</div>
			<div className="mt-10">
				<Checkbox
					checked={shouldHideDropShadow(printOptions)}
					onChange={onCheckOptionChanged("hideDropShadow")}
					label="Hide drop shadow" />
			</div>
			<div className="mt-10">
				<Checkbox
					checked={shouldSortAbilities(printOptions)}
					onChange={onCheckOptionChanged("sortAbilities")}
					label="Sort abilities" />
			</div>
			<div className="mt-10">
				<Checkbox
					checked={shouldShowBlessings(printOptions)}
					onChange={onCheckOptionChanged("showBlessings")}
					label="Show blessings" />
			</div>
			<div className="mt-10">
				<Checkbox
					checked={shouldShowDuplicateFighters(printOptions)}
					onChange={onCheckOptionChanged("showDuplicateFighters")}
					label="Show duplicate fighters" />
			</div>
			<div className="mt-10">
				<Checkbox
					checked={shouldInvertMonsterTables(printOptions)}
					onChange={onCheckOptionChanged("invertMonsterTables")}
					label="Invert monster damage tables" />
			</div>
			<div className="mt-10">
				<Button className="print-button" onClick={onPrint}>
					Print Reference Sheet
				</Button>
			</div>
		</div>
	);
}

export default PrintOptions;
