import { useMemo } from 'react';

import { expandWarband, objectMapToArray } from '../utils';
import { getBlessingById }  from './BlessingData';
import BlessingSelector from './BlessingSelector';

import './Blessings.css';

const BlessingsPage = ({
	fighters, selectedFighters, fighterBlessings, setFighterBlessings
}) => {
	const setBlessing = (fighterId, blessingId, weaponSelection) => {
		setFighterBlessings({
			...fighterBlessings,
			[fighterId]: {
				id: blessingId,
				weaponSelection: weaponSelection
			}
		});
	}

	const normalStyle = {color: "black"};
	const warningStyle = {color: "red"};

	const warband = useMemo(
		() => expandWarband(fighters, selectedFighters),
		[fighters, selectedFighters]
	);

	const fighterPoints = objectMapToArray(selectedFighters, ([key, value]) => 
		((fighters.find(
			(fighter) => +(fighter.id) === +(key)
		) || 0)?.points || 0) * value
	).reduce((a,b) => a + b, 0);

	const fighterBlessingsPoints = warband.map((fighter, idx) => (
		getBlessingById(fighterBlessings[idx]?.id)?.getPointsCost(fighter) ?? 0
	)).reduce((a,b) => a + b, 0);

	const totalPoints = fighterPoints + fighterBlessingsPoints;

	return (
		<div className="Blessings no-print">
			<div style={totalPoints > 1000 ? warningStyle : normalStyle}>
				{totalPoints}{"/1000 points"}
			</div>
			{warband.map((fighter, idx) => (
				<div key={idx}>
					<div className="Blessings-FighterLine">
						<div className="Blessings-FighterName">
							{fighter.name}
						</div>
						<BlessingSelector
							fighter={fighter}
							blessingId={fighterBlessings[idx]?.id}
							weaponSelection={fighterBlessings[idx]?.weaponSelection}
							onChange={(blessingId, weaponSelection) =>
								setBlessing(idx, blessingId, weaponSelection)}
						/>
					</div>
				</div>
			))}
		</div>
	);
}

export default BlessingsPage;
