import {
	completeRunemarkList,

	factionRunemarks,
	bladebornRunemarks,

	orderFactionRunemarks,
	chaosFactionRunemarks,
	deathFactionRunemarks,
	destructionFactionRunemarks,

	orderBladebornRunemarks,
	chaosBladebornRunemarks,
	deathBladebornRunemarks,
	destructionBladebornRunemarks,

	orderGARunemark,
	chaosGARunemark,
	deathGARunemark,
	destructionGARunemark,
} from './runemarkList';

const inList = (name, runemarkList) =>
	runemarkList.some((runemark) => runemark.name === name);

const isOrderRunemark = (name) => 
	name === orderGARunemark.name ||
	inList(name, orderFactionRunemarks) ||
	inList(name, orderBladebornRunemarks);

const isChaosRunemark = (name) =>
	name === chaosGARunemark.name ||
	inList(name, chaosFactionRunemarks) ||
	inList(name, chaosBladebornRunemarks);

const isDeathRunemark = (name) =>
	name === deathGARunemark.name ||
	inList(name, deathFactionRunemarks) ||
	inList(name, deathBladebornRunemarks);

const isDestructionRunemark = (name) =>
	name === destructionGARunemark.name ||
	inList(name, destructionFactionRunemarks) ||
	inList(name, destructionBladebornRunemarks);

export const isFactionRunemark = (name) => 
	inList(name, factionRunemarks) || isBladebornRunemark(name);

export const isBladebornRunemark = (name) => inList(name, bladebornRunemarks);

export const isSameGrandAlliance = (name1, name2) => 
	(isOrderRunemark(name1) && isOrderRunemark(name2)) ||
	(isChaosRunemark(name1) && isChaosRunemark(name2)) ||
	(isDeathRunemark(name1) && isDeathRunemark(name2)) ||
	(isDestructionRunemark(name1) && isDestructionRunemark(name2));

export const getRunemarkByName = (name) => 
	completeRunemarkList.find((runemark) => runemark.name === name);
