import {isAbilityActive, expandWarband} from '../utils';
import {
	shouldSortAbilities,
	shouldShowBlessings,
	shouldShowDuplicateFighters
} from '../state/printOptions';
import {getBlessingById} from '../Blessings/BlessingData';

const ability_order_map = {
	"REACTION": 1,
	"DOUBLE": 2,
	"TRIPLE": 3,
	"QUAD": 4
}

const ability_comparator = (a1, a2) => {
	const hero = "fighters-leader";
	// TODO: Keep factions together?
	if (a1.runemarks.includes(hero) && !a2.runemarks.includes(hero)) {
		return 1;
	} else if (!a1.runemarks.includes(hero) && a2.runemarks.includes(hero)) {
		return -1;
	} else {
		return ability_order_map[a1.type] - ability_order_map[a2.type];
	}
}

const removeDuplicates = (a) => [...new Set(a.map(JSON.stringify))].map(JSON.parse);

export const getFighters = (group, printOptions) => {
	if (shouldShowBlessings(printOptions)) {
		const warband = expandWarband(group.fighters, group.selectedFighters);
		const warbandWithBlessings = warband.map((fighter, idx) =>
			getBlessingById(group.blessings?.[idx]?.id)?.apply?.(
					fighter, group.blessings[idx].weaponSelection
				) ?? fighter);
		if (shouldShowDuplicateFighters(printOptions)) {
			return warbandWithBlessings;
		} else {
			return removeDuplicates(warbandWithBlessings);
		}
	} else {
		const fighters = group.fighters.filter(
			(fighter) => group.selectedFighters[fighter.id]
		);
		if (shouldShowDuplicateFighters(printOptions)) {
			return expandWarband(fighters, group.selectedFighters);
		} else {
			return fighters;
		}
	}
}

export const getAbilities = (group, fighters, printOptions) => {
	const abilities = group.abilities.filter(
		(ability) => isAbilityActive(ability, fighters, group.abilityOverrides)
	);
	if (shouldSortAbilities(printOptions)) {
		abilities.sort(ability_comparator);
	}
	return abilities;
}
