import { useEffect, useMemo, useReducer } from 'react';
import { Tab } from 'semantic-ui-react';

import { updateElementIndex, objectFilter } from './utils';

import { makeDefaultState } from './state/defaultState';
import { updateDataVersion } from './state/data_versioning';
//import testState from './state/testState';
import AbilitySelector from './AbilitySelector/AbilitySelector';
import BlessingsPage from './Blessings/BlessingsPage';
import FighterSelector from './FighterSelector/FighterSelector';
import GroupSelector from './GroupSelector/GroupSelector';
import Preview from './ReferenceSheet/Preview';
import ReferenceSheet from './ReferenceSheet/ReferenceSheet';
import RosterBuilder from './RosterBuilder/RosterBuilder';
import PrintOptions from './PrintOptions/PrintOptions';
import stateReducer from './state/reducer';
import * as actions from './state/actions';


const STATE_KEY = "REFERENCE_SHEET_PAGE_DATA";

const getStoredData = () => {
	const storedData =
		JSON.parse(localStorage.getItem(STATE_KEY)) || makeDefaultState();
	return updateDataVersion(storedData);
}

const ReferenceSheetPage = () => {
	const [ state, dispatch ] = useReducer(stateReducer,
		//testState,
		getStoredData()
	);

	useEffect(() => {
		localStorage.setItem(STATE_KEY, JSON.stringify(state));
	}, [state]);

	const currentGroupIndex = useMemo(() => 
		state.groups.findIndex((group) => group.id === state.currentGroup),
	[state]);
	const currentGroup = useMemo(() =>
		state.groups[currentGroupIndex],
	[state, currentGroupIndex]);

    useEffect(() => {
		const prefix = currentGroup?.name;
		const suffix = "Matt's Warcry Reference Sheet Builder";
		if (prefix && prefix.length > 0) {
			document.title = `${currentGroup.name} - ${suffix}`;
		} else {
			document.title = suffix;
		}
	}, [currentGroup]);

	useEffect(() => {
		if (!currentGroup && state.groups.length > 0) {
			dispatch(actions.setCurrentGroup(state.groups[0].id));
		}
	}, [state, currentGroupIndex, currentGroup]);

	const saveFighter = (fighterToSave) => {
		const index = currentGroup.fighters.findIndex(
			(fighter) => fighter.id === fighterToSave.id
		);
		if (index >= 0) {
			dispatch(actions.updateFighters(
				currentGroup.fighters.map((fighter) =>
					fighter.id === fighterToSave.id ? fighterToSave : fighter)
			));
		} else {
			dispatch(actions.updateFighters(
				[...currentGroup.fighters, fighterToSave]
			));
			dispatch(actions.updateSelectedFighters({
				...currentGroup.selectedFighters,
				[fighterToSave.id]: 1
			}));
		}
	}

	const batchSaveFighters = (fighters) => {
		// When batch saving, don't add the fighters to selected.
		const updatedFighters = [...currentGroup.fighters];
		fighters.forEach((newFighter) => {
			const index = currentGroup.fighters.findIndex(
				(fighter) => fighter.id === newFighter.id
			);
			if (index >= 0) {
				updatedFighters[index] = newFighter;
			} else {
				updatedFighters.push(newFighter);
			}
		});
		dispatch(actions.updateFighters(updatedFighters));
	}

	const updateGroup = (groupData) => {
		const index = state.groups.findIndex(
			(group) => group.id === groupData.id
		);
		if (index >= 0) {
			dispatch(actions.updateGroup(groupData));
		} else {
			dispatch(actions.addGroup(groupData));
		}
	}

	const saveAbility = (abilityToSave) => {
		const index = currentGroup.abilities.findIndex(
			(ability) => ability.id === abilityToSave.id
		);
		if (index >= 0) {
			dispatch(actions.updateAbilities(
				currentGroup.abilities.map((ability) =>
					ability.id === abilityToSave.id ? abilityToSave : ability)
			));
		} else {
			dispatch(actions.updateAbilities(
				[...currentGroup.abilities, abilityToSave]
			));
		}
	}

	const batchSaveAbilities = (abilities) => {
		const updatedAbilities = [...currentGroup.abilities];
		abilities.forEach((newAbility) => {
			const index = currentGroup.abilities.findIndex(
				(ability) => ability.id === newAbility.id
			);
			if (index >= 0) {
				updatedAbilities[index] = newAbility;
			} else {
				updatedAbilities.push(newAbility);
			}
		});
		dispatch(actions.updateAbilities(updatedAbilities));
	}

	const deleteFighter = (id) => {
		dispatch(actions.updateFighters(
			currentGroup.fighters.filter((fighter) => fighter.id !== id),
		));
		dispatch(actions.updateSelectedFighters(
			objectFilter(currentGroup.selectedFighters, ([key]) => key !== id)
		));
	}

	const deleteAbility = (id) => {
		dispatch(actions.updateAbilities(
			currentGroup.abilities.filter((ability) => ability.id !== id)
		));
	}

	const moveFighter = (prevIndex, newIndex) => {
		dispatch(actions.updateFighters(
			updateElementIndex(currentGroup.fighters, prevIndex, newIndex)
		));
	}

	const moveAbility = (prevIndex, newIndex) => {
		dispatch(actions.updateAbilities(
			updateElementIndex(currentGroup.abilities, prevIndex, newIndex)
		));
	}

	const tabPanes = [
		{
			menuItem: "Roster",
			render: () => (
				<Tab.Pane key="roster">
					<RosterBuilder
						otherGroups={[
							...state.groups.slice(0, currentGroupIndex),
							...state.groups.slice(currentGroupIndex + 1)
						]}
						fighters={currentGroup.fighters}
						abilities={currentGroup.abilities}
						saveFighter={saveFighter}
						saveAbility={saveAbility}
						batchSaveFighters={batchSaveFighters}
						batchSaveAbilities={batchSaveAbilities}
						moveFighter={moveFighter}
						moveAbility={moveAbility}
						deleteFighter={deleteFighter}
						deleteAbility={deleteAbility} />
				</Tab.Pane>
			)
		},
		{
			menuItem: "Warband",
			render: () => (
				<Tab.Pane key="FighterSelector">
					<FighterSelector
						fighters={currentGroup.fighters}
						selectedFighters={currentGroup.selectedFighters}
						setSelectedFighters={(selectedFighters) =>
							dispatch(actions.updateSelectedFighters(selectedFighters))
						} />
				</Tab.Pane>
			)
		},
		{
			menuItem: "Abilities",
			render: () => (
				<Tab.Pane key="abilities">
					<AbilitySelector
						fighters={currentGroup.fighters}
						selectedFighters={currentGroup.selectedFighters}
						abilities={currentGroup.abilities}
						abilityOverrides={currentGroup.abilityOverrides}
						setAbilityOverrides={(abilityOverrides) =>
							dispatch(actions.setAbilityOverrides(abilityOverrides))
						}
					/>
				</Tab.Pane>
			)
		},
		{
			menuItem: "Blessings",
			render: () => (
				<Tab.Pane key="Blessings">
					<BlessingsPage
						fighters={currentGroup.fighters}
						selectedFighters={currentGroup.selectedFighters}
						fighterBlessings={currentGroup.blessings ?? {}}
						setFighterBlessings={(blessings) => 
							dispatch(actions.setBlessings(blessings))
						}
					/>
				</Tab.Pane>
			)
		},
		{
			menuItem: "Print",
			render: () => (
				<Tab.Pane key="print">
					<PrintOptions
						printOptions={state.printOptions}
						setPrintOptions={(printOptions) =>
							dispatch(actions.setPrintOptions(printOptions))
						}
					/>
				</Tab.Pane>
			)
		}
	];

	return (
		<div className="ReferenceSheetPage">
			<GroupSelector
				groups={state.groups}
				currentGroup={currentGroup}
				setCurrentGroup={(id) => dispatch(actions.setCurrentGroup(id))}
				updateGroup={updateGroup}
				deleteGroup={(id) => dispatch(actions.deleteGroup(id))}
				/>
			{ currentGroup &&
				<>
					<div className="no-print mt-10">
						<Tab panes={tabPanes} />
					</div>
					<Preview
						group={currentGroup}
						printOptions={state.printOptions} />
					<ReferenceSheet
						group={currentGroup}
						printOptions={state.printOptions} />
				</>
			}
		</div>
	);
}

export default ReferenceSheetPage;
