import { Button, Form, Input, Select, TextArea } from 'semantic-ui-react';

import { useState } from 'react';

import RunemarksEditor from './RunemarksEditor';

const AbilityEditor = ({ability, saveAbility, deleteAbility, exitEditor}) => {
	const [name, setName] = useState(ability.name);
	const [type, setType] = useState(ability.type);
	const [text, setText] = useState(ability.text);
	const [runemarks, setRunemarks] = useState(ability.runemarks);

	const onSave = () => {
		saveAbility({
			id: ability.id,
			name,
			type,
			text,
			runemarks
		});
		exitEditor();
	}

	const onDelete = () => {
		deleteAbility();
		exitEditor();
	}

	return (
		<div className="AbilityEditor">
			<Form>
				<Input
					id="name"
					value={name}
					onChange={(e, {value}) => setName(value)}
					placeholder="Name"
					type="text"/>
				<div className="AbilityEditor-runemark-row">
					<label>Runemarks:</label>
					<RunemarksEditor
						value={runemarks}
						onChange={setRunemarks} />
				</div>
				<Select id="type"
					className="mb-5"
					placeholder="Ability Type"
					value={type}
					onChange={(e, {value}) => setType(value)}
					options={[
						{key: "REACTION", value: "REACTION", text: "Reaction"},
						{key: "DOUBLE", value: "DOUBLE", text: "Double"},
						{key: "TRIPLE", value: "TRIPLE", text: "Triple"},
						{key: "QUAD", value: "QUAD", text: "Quad"},
					]} />
				<TextArea
					id="text"
					placeholder="Ability text..."
					value={text}
					onChange={(e, {value}) => setText(value)}
					rows="5"
					cols="40"
					type="text"/>
				<div className="mt-10">
					<Button onClick={onSave}>Save</Button>
					<Button onClick={onDelete}>Delete</Button>
					<Button onClick={exitEditor}>Exit</Button>
				</div>
			</Form>
		</div>
	)
}

export default AbilityEditor;
