import { DragDropContext, Draggable, Droppable } from '@hello-pangea/dnd';

const RosterDragAndDropList = ({values, title, onEdit, onDelete, onDragEnd}) => {
	return (
		<div className="column">
			<h3>{title}</h3>
			<DragDropContext onDragEnd={onDragEnd}>
				<Droppable droppableId={title}>
					{(provided, snapshot) => (
						<div {...provided.droppableProps} ref={provided.innerRef}>
							{ values.map((value, index) => (
								<Draggable
									key={"" + value.id}
									draggableId={"" + value.id}
									index={index}>
									{(provided, snapshot) => (
										<div
											ref={provided.innerRef}
											{...provided.draggableProps}
											{...provided.dragHandleProps}
										>
											<RosterDragAndDropListItem
												value={value}
												onEdit={onEdit}
												onDelete={onDelete} />
										</div>
									)}
								</Draggable>
								))
							}
							{provided.placeholder}
						</div>
					)}
				</Droppable>
			</DragDropContext>
		</div>
	);
}

const RosterDragAndDropListItem = ({value, onEdit, onDelete}) => (
	<div className="resource-row">
		<span className="grab-handle"></span>
		<button
			className="edit icon-button"
			onClick={() => onEdit(value)} />
		<button 
			className="delete icon-button"
			onClick={() => onDelete(value.id)} />
		<div className="resource-name">
			{value.name}
		</div>
	</div>
)

export default RosterDragAndDropList;
