import { useEffect, useState } from 'react';

import "./NumericInput.css";

const NumericInput = ({
	value, onChange, validate=true, min=0, max=20, increment=1, width="3em", disabled
}) => {
	const [inputValue, setInputValue] = useState(value);

	useEffect(() => {
		setInputValue(value);
	}, [value]);

	const onInputChange = (e) => {
		const inputValue = e.target.value;
		setInputValue(inputValue);
		if (validate) {
			if ((inputValue !== "" && !isNaN(+inputValue))) {
				onChange(enforceBounds(+inputValue));
			}
		} else {
			onChange(inputValue);
		}
	}

	const onBlur = () => {
		if (validate && (inputValue === "" || isNaN(+inputValue))) {
			const enforcedValue = enforceBounds(0);
			setInputValue(enforcedValue);
			onChange(enforcedValue);
		}
	}

	const enforceBounds = (x) => {
		if (validate && x < min) {
			return min;
		} else if (validate && x > max) {
			return max;
		} else {
			return x;
		}
	}

	const decrementValue = () => onChange(enforceBounds(
		isNaN(+value) ? min : (+value) - increment
	));
	const incrementValue = () => onChange(enforceBounds(
		isNaN(+value) ? min + increment : (+value) + increment
	));

	return (
		<div className="NumericInput">
			<button
				className="NumericInput-decrement"
				disabled={disabled || (+inputValue) <= min}
				onClick={decrementValue}>
				-
			</button>
			<input
				disabled={disabled}
				value={inputValue}
				type="text"
				inputMode="numeric"
				pattern="[0-9]*"
				onChange={onInputChange}
				onBlur={onBlur}
				style={{width}}
			/>
			<button
				className="NumericInput-increment"
				disabled={disabled || (+inputValue) >= max}
				onClick={incrementValue}>
				+
			</button>
		</div>
	)
}

export default NumericInput;
