import Runemark from '../Runemarks/Runemark';

const mapType = (type) => ({
	"REACTION": "Reaction",
	"DOUBLE": "Double",
	"TRIPLE": "Triple",
	"QUAD": "Quad"
}[type])

const AbilityCard = ({ability}) => {

	return (
		<div className="ability-row">
			<div className="runemarks">
				{ability.runemarks.map((runemark) => 
					<Runemark key={runemark} runemark={runemark} />
				)}
			</div>
			<div className="ability">
				[{mapType(ability.type)}] {ability.name}: {ability.text}
			</div>
		</div>
	)
}

export default AbilityCard;
